.a-collapsed-icon {
  display: block;
  position: absolute;
  right: 0;
  top: 0.25rem;
  min-width: 24px;
  min-height: 24px;
}

.a-collapsed-icon:hover .a-collapsed-icon__top-line {
  background-color: #0946B0;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-out;
}

.a-collapsed-icon:hover .a-collapsed-icon__bottom-line {
  background-color: #0946B0;
  transform: rotate(45deg);
  transition: transform 0.3s ease-out;
}

.a-collapsed-icon__top-line {
  background-color: #077D15;
  display: block;
  width: 16px;
  height: 4px;
  transform-origin: 0 0;
  transform: rotate(45deg);
  transition: all 0.3s ease-in-out;
}

.a-collapsed-icon__top-line--open {
  background-color: #0946B0;
  transform: translateY(4px) rotate(180deg) rotate(45deg);
  transition: transform 0.3s ease-in-out;
}

.a-collapsed-icon__bottom-line {
  background-color: #9C1126;
  display: block;
  width: 16px;
  height: 4px;
  transform-origin: 0 100%;
  transform: translateX(8px) translateY(8px) rotate(-45deg);
  transition: all 0.3s ease-in-out;
}

.a-collapsed-icon__bottom-line--open {
  background-color: #0946B0;
  transform: translateY(4px) rotate(180deg) rotate(45deg);
  transition: transform 0.3s ease-in-out;
}

