.fig {
  background-color: #EBEFF5;
  background: linear-gradient(180deg, #EBEFF5, #BEC5D1, #EBEFF5);
  background-size: 200% 200%;
  animation: lazy-loader 2s ease-in-out infinite;
  /* Need this to clear the float
   * Does not work without it */
}

.fig:before {
  content: "";
  width: 1px;
  margin-left: -1px;
  float: left;
  height: 0;
  padding-top: 56.25%;
}

.fig:after {
  content: "";
  display: table;
  clear: both;
}

@keyframes lazy-loader {
  0% {
    background-position: 50% 0%;
  }
  50% {
    background-position: 50% 100%;
  }
  100% {
    background-position: 50% 0%;
  }
}

.fig--1x1:before {
  padding-top: 100% !important;
}

.fig--4x3:before {
  padding-top: 75% !important;
}

.fig--3x4:before {
  padding-top: 133% !important;
}

.fig--about:before {
  padding-top: 133% !important;
}

